import React, {Component} from "react";

import AppHeader from "../app-header";
import SearchPanel from "../search-panel";
import TodoList from "../todo-list";
import ItemStatusFilter from "../item-status-filter";
import ItemAddForm from "../item-add-form";

import "./app.css";

export default class App extends Component {
	maxId = 100;

	state = {
		todoData: [
			this.createTodoItem("Drink Coffee"),
			this.createTodoItem("Make Awesome App"),
			this.createTodoItem("Have a lunch")
		],
		term: "",
		filter: "all"
	};

	createTodoItem(label) {
		return {
			label: label,
			important: false,
			done: false,
			id: this.maxId++
		};
	}

	deleteItem = id => {
		this.setState(({todoData}) => {
			const idx = todoData.findIndex(el => el.id === id);

			const newArray = [...todoData.slice(0, idx), ...todoData.slice(idx + 1)];

			return {
				todoData: newArray
			};
		});
	};

	addItem = text => {
		const newItem = this.createTodoItem(text);

		this.setState(({todoData}) => {
			const newArr = [...todoData, newItem];

			return {
				todoData: newArr
			};
		});
	};

	toggleProperty(arr, id, propName) {
		const idx = arr.findIndex(el => el.id === id);

		const oldItem = arr[idx];
		const newItem = {...oldItem, [propName]: !oldItem[propName]};

		return [...arr.slice(0, idx), newItem, ...arr.slice(idx + 1)];
	}

	onToggleImportant = id => {
		this.setState(({todoData}) => {
			// this.toggleProperty(todoData, id, "important");
			// const idx = todoData.findIndex(el => el.id === id);
			// const newArray = [...todoData];
			// newArray[idx].important = !newArray[idx].important;
			// return {
			//   todoData: newArray
			// };
			return {
				todoData: this.toggleProperty(this.state.todoData, id, "important")
			};
		});
	};

	myToggleProperty(arr, id, propName) {
		const idx = arr.findIndex(i => i.id === id);
		const newArray = [...arr];
		const item = newArray[idx];
		item[propName] = !item[propName];
		return newArray;
	}

	onToggleDone = id => {
		this.setState(({todoData}) => {
			// const idx = todoData.findIndex(el => el.id === id);

			// const newArray = [...todoData];
			// newArray[idx].done = !newArray[idx].done;

			return {
				todoData: this.myToggleProperty(this.state.todoData, id, "done")
			};
		});
	};

	search(items, term) {
		if (term.length === 0) {
			return items;
		}
		return items.filter(
			item => item.label.toLowerCase().indexOf(term.toLowerCase()) > -1
		);
	}

	onSearch = term => {
		this.setState({
			term
		});
	};

	filter(items, filter) {
		if (filter === "all") {
			return items;
		} else if (filter === "active") {
			return items.filter(item => !item.done);
		} else if (filter === "done") {
			return items.filter(item => item.done);
		}
	}

	onFilterChange = filter => {
		this.setState({filter});
	};

	render() {
		// let doneCount = 0;
		// this.state.todoData.forEach(i => {
		//   if (i.done) {
		//     doneCount++;
		//   }
		//   return doneCount;
		// });
		const {todoData, term, filter} = this.state;

		const visibleItems = this.filter(this.search(todoData, term), filter);

		const doneCount = todoData.filter(el => el.done).length;
		const todoCount = todoData.length - doneCount;

		return (
			<div className="todo-app">
				<AppHeader toDo={todoCount} done={doneCount}/>
				<div className="top-panel d-flex">
					<SearchPanel onSearch={this.onSearch}/>
					<ItemStatusFilter
						filter={filter}
						onFilterChange={this.onFilterChange}
					/>
				</div>

				<TodoList
					todos={visibleItems}
					onDeleted={this.deleteItem}
					onToggleImportant={this.onToggleImportant}
					onToggleDone={this.onToggleDone}
				/>

				<ItemAddForm onItemAdded={this.addItem}/>
			</div>
		);
	}
}
